import React, { useState } from 'react';
import {
  Paper,
  createStyles,
  TextInput,
  PasswordInput,
  Checkbox,
  Button,
  Title,
  Text,
  Anchor,
} from '@mantine/core';
import axios from 'axios';
import { Loader } from '@mantine/core';
import { AlertCircle, Check } from 'tabler-icons-react';
import Footer from '../Components/Footer';
const useStyles = createStyles((theme) => ({
  wrapper: {
    width:'100%',
    display:'inline-flex'
  },

  form: {
    width:'50%',
    borderRight: `1px solid ${theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.colors.gray[3]
      }`,
    paddingTop: 80,

    [`@media (max-width: ${theme.breakpoints.sm}px)`]: {
      maxWidth: '100%',
    },
  },

  title: {
    color: theme.colorScheme === 'dark' ? theme.white : theme.black,
    fontFamily: `Greycliff CF, ${theme.fontFamily}`,
  },

  logo: {
    color: theme.colorScheme === 'dark' ? theme.white : theme.black,
    width: 120,
    display: 'block',
    marginLeft: 'auto',
    marginRight: 'auto',
  },

  imageContainer:{
    backgroundColor: '#0d1118',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundImage:'url(/pattern.svg)',
    paddingTop: theme.spacing.xl * 3,
    paddingBottom: theme.spacing.xl * 3,
    position: 'relative'
  }
}));

export default function Signup() {
  const { classes } = useStyles();
  const [form, setForm] = useState({
    username: { value: '', error: undefined },
    email: { value: '', error: undefined },
    password: { value: '', error: undefined },
    password_repeat: { value: '', error: undefined },
    phone_number:{value:'', error:undefined},
    iAccept: { value: false, error: undefined }
  }
  );
  const [respmsg, setRespmsg] = useState();
  const [isLoading, setLoading] = useState(false);
  const [usernameIsLoading, setUsernameLoading] = useState(false);

  function handleChange(name, value) {
    const auxForm = { ...form };
    auxForm[name]['value'] = value;
    auxForm[name]['error'] = undefined;
    setForm(auxForm);

  }

  function setError(name, error) {
    const auxForm = { ...form };
    auxForm[name]['error'] = error;
    setForm(auxForm);
  }

  function handleSubmit(e) {
    e.preventDefault();
  
    console.log(form)
    setLoading(true);

    let error = false;
    if (!form['username']['value'] || (form['username']['value'].length == 0)) {
      // Set error
      setError('username', 'Username cannot be empty!')
      error = true;
    }
    if (!form['email']['value'] || (form['email']['value'].length == 0)) {
      // Set error
      setError('email', 'Email cannot be empty!')
      error = true;
    }
    if (!form['password']['value'] || (form['password']['value'].length == 0)) {
      // Set error
      setError('password', 'Password cannot be empty!')
      error = true;
    }
    if (!form['password_repeat']['value'] || (form['password_repeat']['value'] != form['password']['value'])) {
      // Set error
      setError('password_repeat', 'Please type your password again!')
      error = true;
    }

    if(!form['phone_number'].value || (form['phone_number'].value != form['phone_number'].value)){
      setError('phone_number', 'Phone number cannot be empty!')
      error = true;

    }


    if (error) {
      setLoading(false)

      return;
    } else {
      // make api request
      axios.post('http://localhost:8080/api/register', { username: form['username']['value'], email: form['email'].value, password: form['password']['value'], phone_number:form['phone_number'].value })
        .then((resp) => {
          console.log(resp);
          if(resp.data.status&&(resp.data.status=='ok')){
            // success
            window.location = '/login?identifier=' + form['email'].value + '&account_created';
          }else if(resp.data.status&&(resp.data.status=='error')){
            if(resp.data.fields&&(resp.data.fields.length > 0)){
              const errors = resp.data.fields;
              for (let i=0; i<errors.length; i++){
                setError(errors[i].name, errors[i].label);
              }
            }
          }
          setLoading(false)
        })
        .catch((err) => {
          setRespmsg({ status: 'fatal_error', label: 'Internal error, please try again later.' })
          setLoading(false);
        })
    }



  }

  return (
    <>
    <div className={classes.wrapper}>
      <Paper className={classes.form} radius={0} p="lg" pt={0}>
        <Title order={2} className={classes.title}  mt={20} mb={2}>
          Registration
        </Title>
        <Text mb={20} mt={0} color="dimmed" size="sm"  >Buy and Sell Crypto easily and risk free.</Text>

        <TextInput  mb={10} rightSection={usernameIsLoading && <Loader size="xs" />} value={form['username'].value} name="username" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)} label="Username" size="md" error={form['username'].error} />
        <TextInput  mb={10} value={form['email'].value} label="Email address" name="email" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}  size="md" error={form['email'].error} />
        <TextInput  mb={10} value={form['phone_number'].value} label="Phone number" name="phone_number" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}  size="md" error={form['phone_number'].error} />
        
        <PasswordInput  mb={10} value={form['password'].value} label="Password" name="password" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}   size="md" error={form['password'].error} />
        <PasswordInput  mb={10} value={form['password_repeat'].value} label="Repeat Password" name="password_repeat" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}  size="md" error={form['password_repeat'].error} />

        <Checkbox label="I have read and I agree on all the terms and conditions." mt="xl" size="md" checked={form['iAccept'].value == 'on'} name="iAccept" onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)} />
        <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} loading={isLoading} fullWidth mt="xl" size="md" onClick={(e) => handleSubmit(e)}>
          Sign up
        </Button>

        <Text align="center" mt="md">
          You already have an account?{' '}
          <a href="/login" weight={700} >
            Log in
          </a>
        </Text>
      </Paper>
      <div style={{width:'50%'}} className={classes.imageContainer}>

      </div>
    </div>
    <Footer />
    </>
    
  );
}