function Confirmation(props) {
    const [waiting, setWaiting] = useState(false)
    return <Stack>
        <Center>
            <Stack style={{ textAlign: 'center' }} spacing={'xs'}>
                <Trash size={100} style={{ margin: 'auto' }} />
                <Title order={3}>Are you sure you want to reject this deal?</Title>
                <Text style={{ fontSize: '14px', color: '#474747' }}>Rejecting deals few times might come with penalties.</Text>

            </Stack>
        </Center>
        <Group grow mt={20}>
            <Button variant="default" size="md" onClick={(e) => { e.preventDefault(); props.setModal({ opened: false }) }}>Cancel</Button>
            <Button loading={waiting} color="dark" size="md" onClick={(e) => { e.preventDefault(); setWaiting(true); props.onConfirm() }}>Confirm</Button>
        </Group>
    </Stack>;
}

function DealsTable() {
    const { classes, cx } = useStyles();
    const [data, setData] = useState([])
    const [selected, setSelected] = useState(0);
    const [modal, setModal] = useState({ opened: false, component: undefined, title: undefined });
    const [loading, setLoading] = useState(true)

    function pullData() {
        axios.get(`http://localhost:8080/api/deals?token=${sessionStorage.getItem('token')}`)
            .then((resp) => {
                if (resp.data.status == 'ok') {
                    const data = resp.data.data;
                    setData(data);
                    setLoading(false)
                }
            })
            .catch(err => {
                console.log(err)
                setLoading(false)

            })
    }
    useEffect(() => {
        pullData()
    }, [])
    // console.log(parseInt((new Date().getTime() - parseInt(data[0]['creation_time']))/3600 * 100))
    const rows = data.map((row, index) => (
        <div key={row.username} className={classes.row} style={{ borderLeft: '3px solid #087f5b' }}>
            <div className={classes.bodyCase} style={{ width: '15%' }}><div style={{ display: 'inline-flex' }}><div><img style={{ height: '30px', marginRight: '10px', marginTop: '7px' }} src={`/crypto/${row['crypto_id'].toUpperCase()}.svg`} /></div><div style={{ lineHeight: 3 }}>{row['crypto_id']}</div></div></div>
            <div className={classes.bodyCase} style={{ width: '15%' }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>{row['amount']} USD</div></div></div>
            <div className={classes.bodyCase} style={{ width: '10%' }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>{row['sell_quote']}</div></div></div>
            <div className={classes.bodyCase} style={{ width: '20%' }}>
                <div style={{ paddingTop: 14 }}>
                    <Progress value={(new Date().getTime() - parseFloat(row.creation_time)) / 3600000 * 100 > 100 ? 100 : (new Date().getTime() - parseFloat(row.creation_time)) / 3600000 * 100} color="dark" striped animate label={(row.creation_time + 3600000 - new Date().getTime() > 0) ? `${parseInt((row.creation_time + 3600000 - new Date().getTime()) / 60000)} Minutes..` : 'Expired'} size="xl" radius="md" />
                </div>
            </div>
            <div className={classes.bodyCase} style={{ width: '15%' }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>D17 Digipost</div></div></div>
            <div className={classes.bodyCase} style={{ textAlign: 'right', width: '25%' }}>
                <div style={{ marginTop: 10 }}>
                    <Group spacing={2} grow>
                        <Button color="red" variant="subtle" size="xs" leftIcon={<Trash size={14} />} onClick={(e) => { e.preventDefault(); setModal({ opened: true, component: 'reject-confirm', selectedId: row.id }) }}>Reject</Button>
                        <Button color="teal" variant="light" size="xs" onClick={(e) => { e.preventDefault(); setSelected(index); console.log(data[index]); setModal({ opened: true, component: 'deal-details', title: 'Deal details' }) }}>Accept</Button>
                    </Group>

                </div>


            </div>

        </div>
    ));

    function handleRejectDeal(dealId) {
        if (!dealId) {
            alert('No deal is selected');
            return;
        }
        axios.post('http://localhost:8080/api/deals/reject', { token: sessionStorage.getItem('token'), deal_id: dealId })
            .then((resp) => {
                if (resp.data.status == 'ok') {
                    setModal({ opened: false })
                    pullData()
                } else {
                    console.log(resp)
                    alert('Error')
                }
            })
            .catch((err) => {
                console.log(err)
                alert('Error111')
            })

    }
    return (
        <>

            <Modal
                opened={modal.opened}
                onClose={() => setModal({ opened: false })}
                withCloseButton={modal.title}
                title={modal.title}
            >
                {modal.component == 'deal-details' && <DealDetails setModal={setModal} data={data[0]} />}
                {modal.component == 'reject-confirm' && <Confirmation setModal={setModal} onConfirm={() => { handleRejectDeal(modal.selectedId) }} />}
            </Modal>
            <div style={{ position: 'relative', borderBottom: '1px solid whitesmoke' }}>
                <Box sx={{ minWidth: 700 }}>
                    <div className={classes.header}>

                        <div className={classes.headerCase} style={{ width: '15%' }}>
                            Crypto
                        </div>
                        <div className={classes.headerCase} style={{ width: '15%' }}>
                            Amount
                        </div>
                        <div className={classes.headerCase} style={{ width: '10%' }}>
                            Quote
                        </div>
                        <div className={classes.headerCase} style={{ width: '20%' }}>
                            Status
                        </div>

                        <div className={classes.headerCase} style={{ width: '15%' }}>
                            Payment Via
                        </div>
                        <div className={classes.headerCase} style={{ textAlign: 'right', width: '25%' }}>

                            <Button variant="white" color="dark" >Filter</Button>

                        </div>
                    </div>
                    <div className={classes.body}>
                    {loading == true && <div className={classes.row}><Loader size={40} color="dark" style={{ margin: 'auto', marginTop: 100, marginBottom: 100 }} /></div>}
                    {((rows.length > 0) && (loading == false)) && rows}
                    {((rows.length == 0) && (loading == false)) &&
                        <div style={{width:'100%', textAlign:'center', paddingTop:20, paddingBottom:20}}> 
                            <AlertOctagon size={50} />
                            <Title order={4} >You have no pending deals.</Title>
                            <Text style={{opacity:0.6}} mb={10}>Follow the button bellow to publish your first offer!</Text>
                            <Button variant="default" >Show All</Button>
                        </div>}
                    </div>
                    
                </Box>
            </div>
        </>
    );
}

function DataTable(props) {
    const { classes, cx } = useStyles();
    const [scrolled, setScrolled] = useState(false);
    const [data, setData] = useState([])
    const [q, setQ] = useState({})
    const [loading, setLoading] = useState(true)
    // pull data
    function updateData() {

        const params_list = window.location.search.substring(1).split('&').map(item => { return { name: item.split('=')[0], value: item.split('=')[1] || true } });

        let url = `http://localhost:8080/api/offers?`;



        const q = {}
        for (var i = 0; i < params_list.length; i++) {
            if (params_list[i].name && (params_list[i].name == 'crypto_id')) {
                q[params_list[i].name] = params_list[i].value
            }
            if (params_list[i].name && (params_list[i].name == 'amount')) {
                q[params_list[i].name] = params_list[i].value

            }
            if (params_list[i].name && (params_list[i].name == 'max_quote')) {
                q[params_list[i].name] = params_list[i].value


            }
        }

        if (q['crypto_id']) {
            url += `crypto_id=${q['crypto_id']}&`
        }
        if (q['amount']) {
            url += `has_amount=${q['amount']}&`
        }
        if (q['max_quote']) {
            url += `max_sell_quote=${q['max_quote']}`
        }
        axios.get(url)
            .then((resp) => {
                console.log(resp)
                if (resp.data.status == 'ok') {
                    setData(resp.data.data)
                } else if (resp.data.status == 'auth-error') {
                    sessionStorage.clear();
                    window.location = '/login?session_expired'
                }
                setLoading(false)

            })
    }

    useEffect(() => {




        updateData()
        setInterval(updateData, 10000)


    }, [])



    let w = '15%';


    const rows = data.map((row) => (
        <div key={row.username} className={classes.row}>
            <div className={classes.bodyCase} style={{ width: 100 }}><div style={{ display: 'inline-flex' }}><div >
              <Avatar color="dark" radius="xl" size={30}  mt={7} >{row.username.slice(0,2)}</Avatar>
            </div></div></div>
            <div className={classes.bodyCase} style={{ width: w }}><div style={{ display: 'inline-flex' }}><div>
                <img style={{ height: '30px', marginRight: '10px', marginTop: '7px' }} src={`/crypto/${row.crypto_id.toUpperCase()}.svg`} /></div><div style={{ lineHeight: 3 }}>{row.crypto_id}</div></div>
            </div>
            <div className={classes.bodyCase} style={{ width: w }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>
                <div style={{ lineHeight: 1.5 }}>
                    <span><b>Min :</b> {row.min_amount} USD</span><br></br>
                    <span><b>Max :</b> {row.reservoir} USD</span>
                </div>

            </div></div></div>
            <div className={classes.bodyCase} style={{ width: w }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>{row['avg_resp_time'] == -1 ? 'N/C' : row['avg_resp_time']} Minute(s)</div></div></div>
            <div className={classes.bodyCase} style={{ width: w }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>
                <b>{row.sell_quote}</b> TND/USD
            </div></div></div>

            <div className={classes.bodyCase} style={{ width: w }}><div style={{ display: 'inline-flex' }}><div style={{ lineHeight: 3 }}>Digipost D17</div></div></div>

            <div className={classes.bodyCase} style={{ textAlign: 'right', width: w }}>
                <Button onClick={(e) => { e.preventDefault(); window.location = `/buy-crypto?offer_id=${row.id}` }} color="violet" variant="subtle" style={{ margin: 'auto', marginRight: '0px' }}>Buy</Button>
            </div>

        </div>
    ));

    return (
        <div style={{ position: 'relative', borderBottom: '1px solid whitesmoke' }}>
            <Box sx={{ minWidth: 700 }}>
                <div className={classes.header}>

                    <div className={classes.headerCase} style={{ width: 100 }}>
                        Seller
                    </div>
                    <div className={classes.headerCase} style={{ width: w }}>
                        Currency
                    </div>
                    <div className={classes.headerCase} style={{ width: w }}>
                        Reservoir
                    </div>
                    <div className={classes.headerCase} style={{ width: w }}>
                        Avg Response Time
                    </div>

                    <div className={classes.headerCase} style={{ width: w }}>
                        Quote
                    </div>
                    <div className={classes.headerCase} style={{ width: w }}>
                        Payment via
                    </div>

                    <div className={classes.headerCase} style={{ textAlign: 'right', width: w }}>

                        <Button variant="white" color="dark" >Filter</Button>

                    </div>
                </div>
                <div className={classes.body}>
                    {loading ? <div style={{ height: 400, width:'100%',  position: 'relative' }}>
                        <LoadingOverlay visible={loading} />
                    </div> : rows}

                </div>
            </Box>
        </div>
    );
}

function DataTableDashboard(props) {
    const { classes, cx } = useStyles();
    const [scrolled, setScrolled] = useState(false);
    const [data, setData] = useState([])
    const [modal, setModal] = useState({ opened: false, component: undefined, title: undefined });
    const [loading, setLoading] = useState(true);

    // pull data
    function updateData() {
        setLoading(true)
        let url;
        url = `http://localhost:8080/api/offers/dashboard_pull?token=${sessionStorage.getItem('token')}`

        axios.get(url)
            .then((resp) => {
                console.log(resp)
                if (resp.data.status == 'ok') {
                    setData(resp.data.data)
                } else if (resp.data.status == 'auth-error') {
                    sessionStorage.clear();
                    window.location = '/login?session_expired'
                }
                setLoading(false)

            })
    }
    useEffect(() => {
        updateData()
    }, [])

    function handleDelete(selected) {
        if (selected) {
            axios.post(`http://localhost:8080/api/offers/delete`, { token: sessionStorage.getItem('token'), offer_id: selected })
                .then((resp) => {
                    console.log(resp);
                    if (resp.data.status == 'ok') {
                        setModal({ opened: false });
                        updateData();
                    } else {
                        setModal({ opened: false });

                        alert('Offer cannot be deleted.')
                    }
                })
                .catch((err) => {
                    console.log(err);
                    setModal({ opened: false });
                    alert('Offer cannot be deleted, please try again later');

                })
        }
    }

    let w = '15%';


    const rows = data.map((row) => (
        <div key={row.username} className={classes.row}>
            <div className={classes.bodyCase} style={{ width: '10%' }}><div className={classes.vCenter}>{row.id}</div></div>
            <div className={classes.bodyCase} style={{ width: w }}><div className={classes.vCenter} ><div style={{ display: 'inline-flex' }} ><div><img style={{ height: '30px', marginRight: '10px', marginTop: '7px' }} src={`/crypto/${row.crypto_id.toUpperCase()}.svg`} /></div><div style={{ lineHeight: 3 }}>{row.crypto_id}</div></div></div></div>
            <div className={classes.bodyCase} style={{ width: w }}>
                <div className={classes.vCenter}>
                    <span><b>Min :</b> {row.min_amount} USD</span><br></br>
                    <span><b>Max :</b> {row.reservoir} USD</span>
                </div>

            </div>
            <div className={classes.bodyCase} style={{ width: w }}><div className={classes.vCenter}>
                <b>{row.sell_quote}</b> TND/USD
            </div></div>

            <div className={classes.bodyCase} style={{ width: w }}><div className={classes.vCenter}>

                <RingProgress
                    size={60}
                    thickness={7}
                    
                    sections={[{ value: parseFloat(row.amount_sold) / parseFloat(row.reservoir) * 100, color: 'dark' }]}
                    label={
                        <Text color="dark" weight={700} align="center" size="xs">
                            {parseFloat(row.amount_sold)
                              / parseFloat(row.reservoir) * 100}%
                        </Text>
                    }
                />
            </div></div>
            <div className={classes.bodyCase} style={{ width: w }}><div className={classes.vCenter}>
                {row.status}
            </div></div>
            <div className={classes.bodyCase} style={{ textAlign: 'right', width: w }}>
                <div className={classes.vCenter} style={{ right: 10 }}>
                    <Menu>
                        <Menu.Item icon={<Trash size={14} />} onClick={(e) => { e.preventDefault(); setModal({ opened: true, component: 'drop-confirm', target: row.id }); }}>
                            Drop
                        </Menu.Item>
                        <Menu.Item icon={<ArrowDownLeft size={14} />}>
                            Sales
                        </Menu.Item>
                        <Menu.Item icon={<Pencil size={14} />}>
                            Edit
                        </Menu.Item>
                        <Menu.Item icon={<PlayerPause size={14} />}>
                            Pause
                        </Menu.Item>
                    </Menu>
                </div>

            </div>

        </div>
    ));

    return (
        <div style={{ height: 500, position: 'relative', overflowY: 'scroll', borderBottom: '1px solid whitesmoke' }}>
            <Modal
                opened={modal.opened}
                onClose={() => setModal({ opened: false })}
                withCloseButton={modal.title}
                title={modal.title}
            >
                {modal.component == 'drop-confirm' && <Confirmation setModal={setModal} onConfirm={() => { handleDelete(modal.target) }} />}
            </Modal>
            <Box sx={{ minWidth: 200 }}>
                <div className={classes.header}>
                    <div className={classes.headerCase} style={{ width: '10%' }}>
                        #
                    </div>

                    <div className={classes.headerCase} style={{ width: w }}>
                        Currency
                    </div>
                    <div className={classes.headerCase} style={{ width: w }}>
                        Conditions
                    </div>


                    <div className={classes.headerCase} style={{ width: w }}>
                        Quote
                    </div>

                    <div className={classes.headerCase} style={{ width: w }}>
                        Reservoir
                    </div>

                    <div className={classes.headerCase} style={{ width: w }}>
                        Status
                    </div>

                    <div className={classes.headerCase} style={{ textAlign: 'right' }}>

                        <Button variant="white" color="dark" >Filter</Button>

                    </div>
                </div>
                <div className={classes.body}>
                    {loading == true && <div className={classes.row}><Loader size={40} color="dark" style={{ margin: 'auto', marginTop: 100, marginBottom: 100 }} /></div>}
                    {((rows.length > 0) && (loading == false)) && rows}
                    {((rows.length == 0) && (loading == false)) &&
                        <div style={{width:'100%', textAlign:'center', paddingTop:20, paddingBottom:20}}> 
                            <AlertOctagon size={50} />
                            <Title order={4} >You have no listed offers.</Title>
                            <Text style={{opacity:0.6}} mb={10}>Follow the button bellow to publish your first offer!</Text>
                            <Button variant="default" onClick={(e)=>{e.preventDefault(); props.navigate('#offers-create')}}>Create Offer</Button>
                        </div>}
                </div>
            </Box>
        </div>
    );
}



export { DataTable, DealsTable, DataTableDashboard }