import React, { useState } from 'react';
import {
    createStyles,
    Paper,
    Title,
    Text,
    TextInput,
    Button,
    Container,
    Group,
    Anchor,
    Center,
    Box,
    Alert,
    PasswordInput,
    InputWrapper,
    Loader,
} from '@mantine/core';
import { AlertTriangle, ArrowLeft, Check, Mail, Refresh } from 'tabler-icons-react';
import axios from 'axios';

const useStyles = createStyles((theme) => ({
    title: {
        fontSize: 26,
        fontWeight: 900,
        fontFamily: `Greycliff CF, ${theme.fontFamily}`,
    },
    stepTitle: {
        fontWeight: 900,
        fontFamily: `Greycliff CF, ${theme.fontFamily}`,
    },
    controls: {
        [theme.fn.smallerThan('xs')]: {
            flexDirection: 'column-reverse',
        },
    },

    control: {
        [theme.fn.smallerThan('xs')]: {
            width: '100%',
            textAlign: 'center',
        },
    },
}));

export default function Reset() {
    const { classes } = useStyles();
    const [step, setStep] = useState(0)
    const [loading, setLoading] = useState(false)
    const [loading2, setLoading2] = useState(0)
    const [form, setForm] = useState({
        identifier: {
            value: '',
            error: undefined,
        },
        confirmation_code: {
            value: '',
            error: undefined,
        },
        new_password: {
            value: '',
            error: undefined,
        },
        new_password_repeat: {
            value: '',
            error: undefined,
        }
    })



    function handleChange(name, value) {
        var xForm = { ...form }
        xForm[name] = { value: value, error: undefined }
        if(name=="cc1"){
            const cc2Field = document.querySelector(`input[name=cc2]`);
            cc2Field.focus()
        }
        if(name=="cc2"){
            const cc3Field = document.querySelector(`input[name=cc3]`);
            cc3Field.focus()
        }
        if(name=="cc3"){
            const cc4Field = document.querySelector(`input[name=cc4]`);
            cc4Field.focus()
        }
        if (name == "cc4") {
            xForm['confirmation_code'] = { value: xForm['cc1'].value + xForm['cc2'].value + xForm['cc3'].value + xForm['cc4'].value, error: undefined }
        }
        console.log(xForm)
        setForm(xForm)
        
    }
    function setError(field, error) {
        var xForm = { ...form }
        xForm[field] = { value: xForm[field]['value'] || '', error: error }
        setForm(xForm);
    }

    function submitStep0() {
        setLoading(true)
        if (!form['identifier'].value || form['identifier'].value == '') {
            setError('identifier', 'Identifier cannot be empty.')
            setStep(0)
            alert('error')
            return;
        }
        console.log(form)
        axios.post('http://localhost:8080/api/password_reset/generate_code', {
            identifier: form['identifier'].value
        })
            .then((resp) => {
                console.log(resp)
                if (resp.data.status == 'ok') {
                    if(step!=1){
                        setStep(1);

                    }
                    
                } else {
                    alert('Internal error, please try again later.')
                }
                setLoading(false)
                if(loading2==1){
                    setLoading2(2)
                }
            })
            .catch(err => {
                console.log(err)
                setLoading(false)
                
            })
    }

    function submitStep1() {
        setLoading(true)
        alert(form['confirmation_code'].value)
        if (!form['confirmation_code'].value || form['confirmation_code'].value.length != 4) {
            setError('confirmation_code', 'Confirmation code is not valid.')
            setLoading(false);
            console.log(form['confirmation_code'].value)
            return;

        }
        axios.post('http://localhost:8080/api/password_reset/verif_code', {
            identifier: form['identifier'].value,
            reset_code: form['confirmation_code'].value
        })
            .then((resp) => {
                console.log(resp)
                if (resp.data.status == 'ok') {
                    if (resp.data.valid == 1) {
                        // code is valid
                        setLoading(false)
                        setStep(2);

                    } else {
                        setLoading(false)
                        setError('confirmation_code', 'This confirmation code is incorrect or expired.')
                    }
                }
            })
            .catch(err=>{
                alert('Connection Error')
                console.log(err)
            })
    }

    function submitStep2() {
        if (!form['identifier'].value || form['identifier'].value.length == 0) {
            setError('identifier', 'Identifier cannot be empty.')
            setStep(0)
            return;
        }
        if (!form['confirmation_code'].value || form['confirmation_code'].value.length != 4) {
            setError('confirmation_code', 'Confirmation code is not valid.')
            setStep(1)
            return;
        }
        if (!form['new_password'].value || form['new_password'].value.length == 0) {
            setError('new_password', 'New password cannot be empty.')
            setStep(2)
            return;
        }
        if (!form['new_password'].value != form['new_password_repeat'].value) {
            setError('new_password_repeat', 'Identifier cannot be empty.')
            setStep(2)
            return;
        }
        axios.post('http://localhost:8080/api/password_reset/change_password', {
            identifier: form['identifier'].value,
            reset_code: form['confirmation_code'].value,
            new_password: form['new_password'].value
        })
            .then((resp) => {
                if (resp.data.status == 'ok') {
                    window.location = '/login'
                } else {
                    alert('Internal error, please try again later.')
                }
            })
    }


    return (
        <Container size={460} my={30}>


            <Paper withBorder shadow="md" p={30} radius="md" mt="xl">
                {step == 0 && <>
                    <Title order={4} className={classes.stepTitle} mb={2} mt={0}>Password reset</Title>
                    <Text mb={10} color="dimmed" size="sm" >
                        Please type the email address or phone number associated with your account.
                    </Text>
                    <InputWrapper label="Your email" required error={form['identifier']['error']}>
                        <TextInput name="identifier" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} />

                    </InputWrapper>
                    <Group position="apart" mt="lg" className={classes.controls}>
                        <Anchor color="dimmed" size="sm" className={classes.control} onClick={(e) => { e.preventDefault(); window.location = '/login' }}>
                            <Center inline>
                                <ArrowLeft size={12} />
                                <Box ml={5}>Back to login page</Box>
                            </Center>
                        </Anchor>
                        <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} className={classes.control} onClick={(e) => { e.preventDefault(); submitStep0() }}>Send Code</Button>
                    </Group>
                </>}
                {step == 1 && <>

                    <Title order={4} className={classes.stepTitle} mb={2} mt={0}>Confirmation code</Title>
                    <Text mb={10} color="dimmed" size="sm" >
                        Please check your inbox for the confirmation code, in case you did not recieve it please check your spam folder.

                    </Text>
                    <InputWrapper error={form['confirmation_code'].error}>
                        <Group grow>

                            <TextInput name="cc1" styles={{input:{textAlign:'center'}}} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} />
                            <TextInput name="cc2" styles={{input:{textAlign:'center'}}} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} />
                            <TextInput name="cc3" styles={{input:{textAlign:'center'}}} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} />
                            <TextInput name="cc4" styles={{input:{textAlign:'center'}}} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} />

                        </Group>

                    </InputWrapper>

                    <Group position="apart" mt="lg" className={classes.controls}>
                        <Anchor color="dimmed" size="sm" className={classes.control} onClick={(e)=>{e.preventDefault(); setLoading2(1); submitStep0()}}>
                            <Center inline>
                                {loading2==0&&<Refresh size={12} />}
                                {loading2==1&&<Loader size={12} color="gray"/>}
                                {loading2==2&&<Check size={12} />}
                                {loading2==-2&&<AlertTriangle size={12} />}
                                <Box ml={5}>Send new code</Box>
                            </Center>
                        </Anchor>
                        <Button color="dark" loading={loading} variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} className={classes.control} onClick={(e) => { e.preventDefault(); submitStep1() }}>Continue</Button>
                    </Group>
                </>}

                {step == 2 && <>
                    <Title order={4} className={classes.stepTitle} mb={2} mt={0}>Update password</Title>
                    <Text mb={10} color="dimmed" size="sm" >
                        Please make sure to use a strong password.                    </Text>
                    <InputWrapper
                        label="New password"
                        required
                        error={form['new_password'].error}
                    >
                    <PasswordInput name="new_password" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }}  mb={5} />

                    </InputWrapper>

                    <InputWrapper
                        label="Confirm password"
                        required
                        error={form['new_password_repeat'].error}
                    >
                    <PasswordInput name="new_password_repeat" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }}  />                    
                    </InputWrapper>

                    <Group position="apart" mt="lg" className={classes.controls}>

                        <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} className={classes.control} style={{ margin: 'auto', marginRight: 0 }} onClick={(e) => { e.preventDefault(); submitStep2() }}>Update Password</Button>
                    </Group>
                </>}

            </Paper>
        </Container>
    );
}