import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { MantineProvider } from '@mantine/core';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import Login from './Pages/Login';
import HeaderAction from './Components/Navbar';
import Home from './Pages/Home';
import Footer from './Components/Footer';
import Signup from './Pages/Register';
import BuyCrypto from './Pages/BuyCrypto';
import Reservoir from './Pages/Reservoir';
import Dashboard from './Pages/Dashboard';
import CreateOffer from './Pages/CreateOffer';
import axios from 'axios';
import Reset from './Pages/Reset';


const data = [{
  label: 'Bitcoin',
  code: 'BTC',
  reservoir: '321.2 BTC',
  bestQ: '3.2',
  link: '/offers/bitcoin',
},
{
  label: 'Tether',
  code: 'USDT',
  reservoir: '432 K',
  bestQ: '3.1',
  link: '/offers/tether',
},
{
  label: 'Ethereum',
  code: 'ETH',
  reservoir: '201 K',
  bestQ: '3.1',
  link: '/offers/ethereum',
},
{
  label: 'Binance',
  code: 'BNB',
  reservoir: '201 K',
  bestQ: '3.1',
  link: '/offers/binance',
},
]

export default function App() {
  const [notifs, setNotifs] = useState([])
  const [notifsLastSync, setNotifsLastSync] = useState(0);

  function pullData() {
    axios.get(`http://localhost:8080/api/notifications?token=${sessionStorage.getItem('token')}&lastSyncTime=${notifsLastSync}`)
      .then((resp) => {
        // append rows
        console.log(resp)
        if (resp.data.status == 'ok' && resp.data.data.length > 0) {
          console.log('New notification :')
          console.log(resp);

          const xNotifs = [...notifs];
          const responseData = resp.data.data;

          setNotifs(responseData.concat(xNotifs))
          console.log(notifs)


          const ct = new Date().getTime();
          setNotifsLastSync(ct);


        }
        else if (resp.data.status == 'auth-error') {
          sessionStorage.clear();
          window.location = '/login?session_expired';
        }
      })
  }

  useEffect(() => {
    if(sessionStorage.getItem('token')){
      pullData()

      const timerId = setInterval(() => {
        pullData()
  
      }, 10000);
      return () => clearInterval(timerId);
    }
    
  });

  return (
    <div className="App">
      {window.location.href.indexOf('control-panel')==-1&&<HeaderAction notifs={notifs} links={[{ link: '/', label: 'Home', links: [] }, { link: '/', label: 'Buy Crypto', links: data }, { link: '/sell', label: 'Sell Crypto' }, { link: '/vip', label: 'For traders' }, { link: '/faq', label: 'FAQ' }]} />}
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Signup />} />
          <Route path="/buy-crypto" element={<BuyCrypto />} />
          <Route path="/reservoir" element={<Reservoir />} />
          <Route path="/dashboard" element={<Dashboard notifs={notifs} />} />
          <Route path="/createOffer" element={<CreateOffer />} />
          <Route path="/reset" element={<Reset />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

