function OfferSearch(props) {
    const data = [{

        label: 'Bitcoin',
        value: 'BTC',
    },
    {
        label: 'Tether',
        value: 'USDT',
    },
    {
        label: 'Ethereum',
        value: 'ETH',
    },
    {
        label: 'Binance',
        value: 'BNB',
    },
    ]
    const SelectItem = React.forwardRef(
        ({ label, value, reservoir, bestQ, ...others }, ref) => (
            <div ref={ref} key={value} {...others}>
                <Group noWrap>
                    <Avatar src={`/crypto/${value.toUpperCase()}.svg`} />

                    <div>
                        <Text>{label}</Text>
                    </div>
                </Group>
            </div>
        )
    );
    return (
        <Group style={props.style}>
            <Select

                value='All'
                leftSection={<CurrencyEthereum size={14} />}
                itemComponent={SelectItem}
                data={data}
                defaultValue="Bitcoin"
                searchable
                maxDropdownHeight={200}
                size="lg"
                styles={{
                    input: {
                        border: 'none',
                        backgroundColor: '#ffffff24',
                        fontSize: '15px'
                    }
                }}
            />
            <TextInput placeholder="Amount in USD.." size="lg" styles={{
                input: {
                    border: 'none',
                    backgroundColor: '#ffffff24',
                    fontSize: '15px',
                    color: 'white'
                }
            }} />
            <Button color="dark" size="lg" variant="gradient" leftIcon={<Search />}
                gradient={{ from: '#4776E6', to: '#8E54E9' }}>Explore Offers</Button>
        </Group>
    )
}


