function CryptoCard({ name, code, reservoir, bestQ }) {
    return (
        <Box sx={(theme) => ({
            height: '120px',
            borderRadius: theme.radius.md,
            backgroundColor: 'white',
            display: 'inline-flex',
            width: 'max-content',
            paddingRight: '20px',
            marginRight: '10px',
            cursor: 'pointer',
            boxShadow: '0px 0px 5px 0px #0d111824'
        })}
            onClick={(e) => { e.preventDefault(); window.location = `/reservoir?crypto_id=${code}` }}
        >
            <div style={{ padding: '20px', width: 120 }}>
                <img style={{ height: '100%' }} src={`/crypto/${code}.svg`} />

            </div>
            <div style={{ margin: 'auto' }}>
                <Title order={4}>{name} <span style={{ opacity: '0.5' }}>{code}</span></Title>
                <div style={{ display: 'inline-flex' }}>
                    <div style={{ width: 'max-content', marginRight: '15px' }}>
                        <span style={{ opacity: '0.5', fontSize: '12px' }}>OUR RESERVOIR</span>
                        <Title order={4}>{reservoir}</Title>
                    </div>
                    <div style={{ width: 'max-content' }}>
                        <span style={{ opacity: '0.5', fontSize: '12px' }}>BEST QUOTE</span>
                        <Title order={4}>{bestQ}</Title>

                    </div>
                </div>
            </div>


        </Box>
    )
}

