import React, { useEffect, useState } from 'react';
import {
  TextInput,
  PasswordInput,
  Checkbox,
  Anchor,
  Paper,
  Title,
  Text,
  Container,
  Group,
  Button,
  Alert,
  createStyles,
} from '@mantine/core';
import axios from 'axios';
import { AlertCircle, ExclamationMark, Mail } from 'tabler-icons-react';
import { Notification } from '@mantine/core';
import { Transition } from '@mantine/core';


const useStyles = createStyles((theme) => ({
  title: {
    fontSize: 26,
    fontWeight: 900,
    fontFamily: `Greycliff CF, ${theme.fontFamily}`,
  },
  stepTitle: {
    fontWeight: 900,
    fontFamily: `Greycliff CF, ${theme.fontFamily}`,
  },

}));



export default function Login() {
  const { classes } = useStyles();

  const [form, setForm] = useState({ identifier: { value: '', error: undefined }, password: { value: '', error: undefined } });
  const [respmsg, setRespmsg] = useState();
  const [isLoading, setLoading] = useState(false);
  const [mailNotifOpened, setMN] = useState(false);
  const [sessionExpiredNotifOpen, setSEN] = useState(false);

  const [redirect_to, setRedirectTo] = useState('/dashboard')


  useEffect(() => {
    const params_list = window.location.search.substring(1).split('&').map(item => { return { name: item.split('=')[0], value: item.split('=')[1] || true } });

    for (var i = 0; i < params_list.length; i++) {
      if (params_list[i].name && (params_list[i].name == 'identifier')) {
        handleChange('identifier', params_list[i].value);
      }
      if (params_list[i].name && (params_list[i].name == 'account_created')) {
        setMN(true)
      }
      if (params_list[i].name && (params_list[i].name == 'session_expired')) {
        setSEN(true);
      }
      if (params_list[i].name && (params_list[i].name == 'redirect_to')) {
        setRedirectTo(atob(params_list[i].value))

      }
    }




  }, [])

  function handleChange(name, value) {
    const auxForm = { ...form };
    auxForm[name]['value'] = value;
    auxForm[name]['error'] = undefined;
    setForm(auxForm);
    console.log(form);

  }
  function setError(name, error) {
    const auxForm = { ...form };
    auxForm[name]['error'] = error;
    setForm(auxForm);
  }

  function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);

    let error = false;
    if (!form['identifier']['value'] || (form['identifier']['value'].length == 0)) {
      // Set error
      setError('identifier', 'Identifier cannot be empty!')
      error = true;
    }
    if (!form['password']['value'] || (form['password']['value'].length == 0)) {
      // Set error
      setError('password', 'Password cannot be empty!')
      error = true;
    }

    if (error) {
      setLoading(false)

      return;

    }
    console.log({ identifier: form['identifier']['value'], password_hash: form['password']['value'] });

    // make api request
    axios.post('http://localhost:8080/api/login', { identifier: form['identifier']['value'], password: form['password']['value'] })
      .then((resp) => {
        console.log(resp);
        if (resp.data['status'] && (resp.data['status'] == 'ok')) {
          // Login success save info to sessionStorage
          sessionStorage.setItem('username', resp.data['username']);
          sessionStorage.setItem('email', resp.data['email']);
          sessionStorage.setItem('token', resp.data['token']);

          window.location = redirect_to;
        }
        else if (resp.data['status'] && (resp.data['status'] == 'error') && (resp.data['label'] == 'invalid_credentials')) {
          setRespmsg({ status: 'error', label: 'Identifier and/or password is incorrect.' })
        }
        setLoading(false)
      })
      .catch((err) => {
        setRespmsg({ status: 'fatal_error', label: 'Internal error, please try again later.' })
        setLoading(false);
      })

  }

  return (


    <Container size={420} my={40}>
      <Transition mounted={mailNotifOpened} transition="slide-up" duration={400} timingFunction="ease">
        {(styles) => (

          <Notification icon={<Mail size={18} />} color="teal" title="Account is created" style={{ position: 'fixed', bottom: '20px', right: '20px', zIndex: 4 }}>
            Account created, please check your inbox for a verification link.
          </Notification>

        )}
      </Transition>
      <Transition mounted={sessionExpiredNotifOpen} transition="slide-up" duration={400} timingFunction="ease">
        {(styles) => (

          <Notification icon={<ExclamationMark size={18} />} color="red" title="Session expired." style={{ position: 'fixed', bottom: '20px', right: '20px', zIndex: 4 }}>
            Ops, your session token expired, please log in again.
          </Notification>

        )}
      </Transition>



      <Paper withBorder shadow="md" p={30} mt={30} radius="md">
        <Title order={4} className={classes.stepTitle} mb={2} mt={0}>Log In</Title>
        <Text mb={10} color="dimmed" size="sm" >
          Welcome at our virtual trading port where you find the biggest crypto marketplace in Tunisia.
        </Text>
        <TextInput name="identifier" value={form['identifier'].value} error={form['identifier']['error']} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} label="Email, Username or Phone number" required />
        <PasswordInput name="password" value={form['password'].value} error={form['password']['error']} onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} label="Password" required mt="md" />
        <Group position="apart" mt="md">
          <Checkbox label="Remember me" />
          <a href="/reset" >
            <Text size="sm">
              Forgot password?
            </Text>
          </a>
        </Group>
        <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} loading={isLoading} fullWidth mt="xl" color="dark" onClick={(e) => { handleSubmit(e) }}>
          Sign in
        </Button>

        {respmsg && <Alert color="red" mt={5} transition="fade" icon={<AlertCircle size={16} />}>
          {respmsg.label}
        </Alert>}
      </Paper>
    </Container>
  );
}