import React, { useEffect, useState } from 'react';
import {
  createStyles,
  Menu,
  Center,
  Header,
  Container,
  Group,
  Button,
  Burger,
} from '@mantine/core';
import { useBooleanToggle } from '@mantine/hooks';
import { Bell, ChevronDown, Dashboard, DoorExit, Notification, Settings } from 'tabler-icons-react';
import Brand from './../brand.svg';
import { Avatar } from '@mantine/core';
import { ActionIcon } from '@mantine/core';
import { Indicator } from '@mantine/core';
import { Text } from '@mantine/core';
import axios from 'axios';
import { Popper } from '@mantine/core';
import Notifications from './Notifications';
import { Paper } from '@mantine/core';

const HEADER_HEIGHT = 60;

const useAudio = () => {
  const [audio] = useState(new Audio('http://localhost:3000/Notification.mp3'));
  const [playing, setPlaying] = useState(false);

  const play = () => setPlaying(true);

  useEffect(() => {
      playing ? audio.play() : audio.pause();
    },
    [playing]
  );

  useEffect(() => {
    audio.addEventListener('ended', () => setPlaying(false));
    return () => {
      audio.removeEventListener('ended', () => setPlaying(false));
    };
  }, []);

  return [playing, play];
};

const useStyles = createStyles((theme) => ({
  inner: {
    height: HEADER_HEIGHT,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: `0px solid ${theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.colors.gray[3]
      }`
  },

  links: {
    [theme.fn.smallerThan('sm')]: {
      display: 'none',
    },
  },

  burger: {
    [theme.fn.largerThan('sm')]: {
      display: 'none',
    },
  },

  link: {
    display: 'block',
    lineHeight: 1,
    padding: '8px 12px',
    borderRadius: theme.radius.sm,
    textDecoration: 'none',
    color: theme.colorScheme === 'dark' ? theme.colors.dark[0] : theme.colors.gray[7],
    fontSize: theme.fontSizes.sm,
    fontWeight: 500,

    '&:hover': {
      backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[6] : theme.colors.gray[0],
    },
  },

  linkLabel: {
    marginRight: 5,
  },
}));



export default function HeaderAction({ links, ...props}) {
  const { classes } = useStyles();
  const [opened, toggleOpened] = useBooleanToggle(false);
  const [referenceElement, setReferenceElement] = useState(null);
  const [visible, setVisible] = useState(true);
  const [newNotif, setNewNotif] = useState(false);
  const [playing, play] = useAudio();

  function handleLogout(e){
    e.preventDefault();
    // clear session storage
    sessionStorage.clear();
    // redirect
    window.location = '/';
  }



  useEffect(()=>{
    const token = sessionStorage.getItem('token');
    if(token){
      axios.post('http://localhost:8080/api/verif_token', {token})
      .then(({data})=>{
        if(data.status && (data.status=='ok')){
          // session is valid
          // run notif watch dog
          


        }else{
          sessionStorage.clear()
          window.location = '/login?session_expired';
          
        }
      })
      .catch((err)=>{
        console.log(err);
      })
    }
    
  },[])

  useEffect(()=>{
    if(props.notifs.length > 0){
      if(props.notifs[0].seen == 1){
        // if seen
        setNewNotif(false)
      }else{
        play()
        setNewNotif(true)

      }
    }
  }, [props.notifs])
  
  const items = links.map((link) => {
    let menuItems = undefined;

    if (link.links && link.links.length > 0) {
      menuItems = link.links.map((item) => (
        <Menu.Item key={item.link}>
          <Group noWrap>
            <Avatar src={`/crypto/${item.code.toUpperCase()}.svg`} />

            <div>
              <Text size="sm">{item.label} </Text>
              <Text size="xs" color="dimmed">
                {item.code}
              </Text>
            </div>
          </Group>
        </Menu.Item>
      ));
    }


    if (menuItems) {
      return (
        <Menu
          key={link.label}
          trigger="hover"
          delay={0}
          transitionDuration={0}
          placement="end"
          gutter={1}
          control={
            <a
              href={link.link}
              className={classes.link}
              onClick={(event) => event.preventDefault()}
            >
              <Center>
                <span className={classes.linkLabel}>{link.label}</span>
                <ChevronDown size={12} />
              </Center>
            </a>
          }
        >
          {menuItems}
        </Menu>
      );
    }

    return (
      <a
        key={link.label}
        href={link.link}
        className={classes.link}
        onClick={(event) => event.preventDefault()}
      >
        {link.label}
      </a>
    );
  });

  return (
    <Header height={HEADER_HEIGHT} sx={{position:'sticky'}} >
      <Container className={classes.inner} fluid>
        <Group>
          <Burger
            opened={opened}
            onClick={() => toggleOpened()}
            className={classes.burger}
            size="sm"
          />
          <a href="/">
            <img src={Brand} style={{ height: '30px' }} />
          </a>
        </Group>
        <Group spacing={5} className={classes.links}>
          {items}
        </Group>


        {sessionStorage.getItem('token') ? <Group>
          <ActionIcon size="lg" ref={setReferenceElement} radius="xl" onClick={(e)=>{e.preventDefault();}} >
            
            {newNotif?<Indicator color="red"  >
              <Bell />
            </Indicator>:<Bell />}
          </ActionIcon>
          
          <Menu
            control={<Center style={{ cursor: 'pointer' }}>
              <Avatar color="dark" radius="xl" mr={10}>{sessionStorage.getItem('username').slice(0,2)}</Avatar>
              <span className={classes.linkLabel}>{sessionStorage.getItem('username')}</span>
              <ChevronDown ml={20} size={12} />
            </Center>}
          >

            <Menu.Item onClick={(e)=>{e.preventDefault(); window.location = '/dashboard'}} icon={<Dashboard size={14} />}>Dashboard</Menu.Item>

            <Menu.Item onClick={(e)=>{e.preventDefault(); window.location = '/dashboard#settings'}} icon={<Settings size={14} />}>Settings</Menu.Item>
            <Menu.Item icon={<DoorExit size={14} />} onClick={(e) => handleLogout(e)}>Log out</Menu.Item>
          </Menu>

        </Group> : <Group>

          <Button onClick={(e) => { e.preventDefault(); window.location = '/login' }} radius="xl" color="dark" variant="subtle" style={{height:30}}>
            Log in
          </Button>
          <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} onClick={(e) => { e.preventDefault(); window.location = '/register' }} radius="xl" style={{height:30}}>
            Sign up
          </Button>
        </Group>}

      </Container>
    </Header>
  );
}