import { InputWrapper } from '@mantine/core';
import { Button } from '@mantine/core';
import { Group } from '@mantine/core';
import { Input, Text } from '@mantine/core';
import { Accordion } from '@mantine/core';
import React from 'react';
import { CreditCard, History, ShieldLock, UserCircle } from 'tabler-icons-react';


const Icons = {
    'General':UserCircle,
    'Security':ShieldLock,
    'Payment':CreditCard,
    'History':History
}

export default function Settings() {

    function AccordationLabel({icon, title}) {
        const Icon = Icons[icon]
        return (    
        <Group noWrap>
            <Icon />
            <div>
                <Text>{title}</Text>
            </div>
        </Group>)
        
    }


    return (
        <Accordion iconPosition="right" style={{backgroundColor:'white', borderRadius:8, border:'1px solid whitesmoke'}}>
            <Accordion.Item label={<AccordationLabel icon='General' title='General information' />}>
                <InputWrapper
                    label="Email Address"
                >
                    <Input />
                </InputWrapper>
                <InputWrapper
                    label="Phone Number"
                >
                    <Input />
                </InputWrapper>
                <div style={{ width: '100%', textAlign: 'right' }}>
                    <Button color="dark" mt={10} >Save</Button>

                </div>
            </Accordion.Item>

            <Accordion.Item label={<AccordationLabel icon='Security' title='Security' />}>
                <InputWrapper
                    label="Password"
                >
                    <Input />
                </InputWrapper>
                <InputWrapper
                    label="Repeat Password"
                >
                    <Input />
                </InputWrapper>
                <div style={{ width: '100%', textAlign: 'right' }}>
                    <Button color="dark" mt={10} >Save</Button>

                </div>
            </Accordion.Item>

            <Accordion.Item label={<AccordationLabel icon='Payment' title='Payment options' />}>
                With new :focus-visible pseudo-class focus ring appears only when user navigates with keyboard
            </Accordion.Item>
            <Accordion.Item label={<AccordationLabel icon='History' title='Log in history' />}>
                With new :focus-visible pseudo-class focus ring appears only when user navigates with keyboard
            </Accordion.Item>
        </Accordion>
    );
}