

import logo from './../logo.svg';
import { ActionIcon, Group, Text, Title } from '@mantine/core';
import React, { useEffect } from 'react';
import { Button } from '@mantine/core';
import { InputWrapper } from '@mantine/core';
import { Input } from '@mantine/core';
import { Card } from '@mantine/core';
import { NumberInput } from '@mantine/core';
import { Stepper } from '@mantine/core';
import { useState } from 'react';
import { MultiSelect, Highlight } from '@mantine/core';
import { Select } from '@mantine/core';

import PayImage from './../resources/pay.png'
import { Loader } from '@mantine/core';
import { Checkbox } from '@mantine/core';
import Login from './../Pages/Login';
import { Container } from '@mantine/core';
import axios, { AxiosError } from 'axios';
import { AlertTriangle, CircleCheck, ExclamationMark, FidgetSpinner, InfoCircle, ThumbDown, ThumbUp } from 'tabler-icons-react';
import { Alert } from '@mantine/core';


function BuyPanel() {
  const [active, setActive] = useState(0);
  const nextStep = () => setActive((current) => (current < 4 ? current + 1 : current));
  const prevStep = () => setActive((current) => (current > 0 ? current - 1 : current));
  const [form, setForm] = useState({
    step1: {
      'buy-amount': undefined
    },
    step2: {

    }
  })
  const [dealId, setDealId] = useState()
  const [currentSystemFeeRate, setCurrentSystemFeeRate] = useState(-1)

  const [offerInfo, setOfferInfo] = useState({
    id: 0,
    crypto_id: undefined,
    reservoir: undefined,
    min_amount: undefined,
    max_amount: undefined,
    sell_quote: undefined,
  })
  const [errors, setErrors] = useState({})

  function setError(field, error) {
    const xErrors = { ...errors }
    xErrors[field] = error;
    setErrors(xErrors)
  }






  function handleChange(name, value) {
    var xForm = { ...form };
    xForm[name] = value;

    setForm(xForm)
  }

  useEffect(() => { }, [form])

  function handleStepSubmit(sub_form) {

    if (active == 0) {
      if (!sub_form['buy-amount'] || sub_form['buy-amount'] <= 0) {
        setError('buy-amount', 'Invalid value for amount.')
        setActive(0)
        return;
      }
      var xForm = { ...form }
      xForm['step1'] = sub_form;
      setForm(xForm);
      nextStep();

    } else if (active == 1) {

      if (!sub_form['wallet-adr'] || sub_form['wallet-adr'].length == 0) {
        setError('wallet-adr', 'Wallet Address cannot be empty.')
        setActive(1)

        return;

      }
      if (!sub_form['phone-num'] || sub_form['phone-num'].length == 0) {
        setActive(1)

        setError('phone-num', 'D17 Number cannot be empty.')
        return;

      }
      var xForm = { ...form }
      xForm['step2'] = sub_form;
      setForm(xForm);
      // create deal api
      axios.post(`http://localhost:8080/api/deals/create`, {
        token: sessionStorage.getItem('token'),
        amount: form['step1']['buy-amount'],
        offer_id: offerInfo['id'],
        wallet_adr: sub_form['wallet-adr'],
        recv_num: sub_form['phone-num']
      })
        .then((resp) => {
          if (resp.data.status == 'ok') {
            window.location = window.location + '&deal_id=' + resp.data.deal_id
          } else if (resp.data.status == 'auth-error') {
            sessionStorage.clear()
            window.location = `/login?session_expired&redirect_to=${btoa(window.location)}`
          } else {
            console.log(resp)
            alert('Internal error, please try again later.')
          }
        })
        .catch(err => {
          console.log(err)
          alert('Internal error, please try again later.')

        })
    } else if (active == 2) {
      // create Charge

      axios.post('http://localhost:8080/api/deals/set/buyer-placed', {
        token: sessionStorage.getItem('token'),
        deal_id: dealId
      })
        .then((resp) => {
          if (resp.data.status == 'ok') {
            nextStep()
          } else {
            console.log(resp)
            alert('Internal error, please try again later.')
          }
        })
    } else {
      nextStep();
    }


  }






  function fetchOfferInfo(offer_id) {
    // verif offer
    axios.get(`http://localhost:8080/api/offers/offer_details?offer_id=${offer_id}`)
      .then((resp) => {
        console.log(resp)
        if (resp.data.status == 'ok') {
          setOfferInfo({
            id: offer_id,
            crypto_id: resp.data['crypto_id'],
            reservoir: resp.data['reservoir'],
            min_amount: resp.data['min_amount'],
            max_amount: resp.data['max_amount'],
            sell_quote: resp.data['sell_quote']
          })
          console.log(offerInfo)
        } else {
          window.location = '/offer_not_found';
        }
      })

  }

  useEffect(() => {


    const params_list = window.location.search.substring(1).split('&').map(item => { return { name: item.split('=')[0], value: item.split('=')[1] || true } });

    for (var i = 0; i < params_list.length; i++) {
      if (params_list[i].name && (params_list[i].name == 'offer_id')) {
        fetchOfferInfo(params_list[i].value)
      }
      if (params_list[i].name && (params_list[i].name == 'deal_id')) {
        setDealId(params_list[i].value)
        setActive(2)
      }
    }



    // fetch current system fee rate
    axios.get('http://localhost:8080/api/params/system-fee')
      .then((resp) => {
        if (resp.data) {
          setCurrentSystemFeeRate(parseFloat(resp.data))
        } else {
          alert('Could not fetch current system fee quote. Please try again later.')
        }
      })
  }, [])



  function Step1(props) {
    const [form, setForm] = useState(props.form);
    const [errors, setErrors] = useState({})

    function setError(field, error) {
      const xErrors = { ...errors }

      if (error == undefined) {
        delete xErrors[field]
      } else {
        xErrors[field] = error;
      }

      setErrors(xErrors)

    }

    function handleChange(name, value) {

      const xForm = { ...form };
      xForm[name] = value;
      if (errors[name]) {
        setError(name, undefined)
      }
      if (name == 'buy-amount') {
        if ((parseFloat(xForm['buy-amount']) < parseFloat(offerInfo.min_amount)) || (parseFloat(xForm['buy-amount']) > parseFloat(offerInfo.max_amount))) {
          setError('buy-amount', 'Selected amount is out of the allowed range.')
        }
        if (!parseFloat(xForm['buy-amount'])) {
          setError('buy-amount', 'Amount cannot be empty.')
        }

        xForm['buy-fee'] = parseFloat(parseFloat(value) * offerInfo['sell_quote'] * currentSystemFeeRate / 100).toFixed(2);
        xForm['buy-pay'] = parseFloat(parseFloat(value) * offerInfo['sell_quote'] + parseFloat(xForm['buy-fee'])).toFixed(2)

      }
      setForm(xForm);

      console.log(xForm)

    }

    return (
      <div style={{ height: '380px', position: 'relative' }}>
        <Title order={4} >Select amount </Title>

        <InputWrapper
          id="buy-amount"
          required
          label={`Amount to buy (USD worth of ${offerInfo['crypto_id']}) :`}
          error={errors['buy-amount']}
          mt={5}
          mb={5}
        >
          <Input id="buy-amount"
            placeholder={`Minimum ${offerInfo.min_amount} USD, Maximum ${offerInfo.max_amount} USD.`}
            name="buy-amount" value={form['buy-amount']} onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)} />
        </InputWrapper>

        <InputWrapper
          id="buy-quote"
          required
          label="Rate :"
          mb={5}

        >
          <Input id="buy-quote" readOnly name="buy-quote" value={offerInfo['sell_quote']} />
        </InputWrapper>


        <InputWrapper
          id="buy-fee"
          label={`System fee (${currentSystemFeeRate}%):`}
          mb={5}
        >
          <Input id="buy-fee" readOnly name="buy-fee" value={form['buy-fee']} />
        </InputWrapper>

        <InputWrapper
          id="buy-pay"
          required
          label="Subtotal (TND) :"
          mb={5}
        >
          <Input id="buy-pay" readOnly name="buy-pay" value={form['buy-pay']} />
        </InputWrapper>

        <div style={{ width: '100%', position: 'absolute', bottom: 0 }}>
          <Button variant="default" onClick={(e) => { e.preventDefault(); props.prevStep() }} size="md" disabled={(active == 0) || (active == 3)}>Previous</Button>
          <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} onClick={(e) => { e.preventDefault(); props.handleStepSubmit(form) }} disabled={Object.keys(errors).length > 0} size="md" style={{ margin: 'auto', marginRight: '0px', float: 'right' }}>Next</Button>

        </div>
      </div>
    )
  }


  function Step2(props) {

    const [form, setForm] = useState(props.form);
    const [errors, setErrors] = useState({})

    function setError(field, error) {
      const xErrors = { ...errors }

      if (error == undefined) {
        delete xErrors[field]
      } else {
        xErrors[field] = error;
      }

      setErrors(xErrors)

    }

    function handleChange(name, value) {
      var xForm = { ...form };
      xForm[name] = value;
      setForm(xForm);
    }

    return (
      <div style={{ height: '380px', position: 'relative' }}>
        <Title order={4}>Payment details</Title>


        <InputWrapper
          id="wallet-adr"
          required
          error={errors['wallet-adr']}
          label={`Your ${offerInfo['crypto_id']} Wallet Address :`}
          description="Please be careful while entering the address, any wrong character will lead to permanat funds loss."
          mt={5}
          mb={5}
        >
          <Input
            value={form['wallet-adr']}
            name="wallet-adr"
            onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}

          />
        </InputWrapper>

        <InputWrapper
          id="phone-num"
          required
          error={errors['phone-num']}
          label="Your Phone Number :"
          description="Please enter the phone number of your D17 transaction."
          mt={5}
          mb={5}
        >
          <Input
            value={form['phone-num']}
            name="phone-num"
            onChange={(e) => handleChange(e.currentTarget.name, e.currentTarget.value)}

          />
        </InputWrapper>

        <div style={{ width: '100%', position: 'absolute', bottom: 0 }}>
          <Button variant="default" onClick={(e) => { e.preventDefault(); props.prevStep() }} size="md" disabled={(active == 0) || (active == 3)}>Previous</Button>
          <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} onClick={(e) => { e.preventDefault(); props.handleStepSubmit(form) }} disabled={(active == 2) || (active == 3)} size="md" style={{ margin: 'auto', marginRight: '0px', float: 'right' }}>Next</Button>

        </div>


      </div>
    )
  }



  function Step3(props) {
    const [paymentInfo, setPaymentInfo] = useState({
      total: undefined,
      phone_number: undefined
    })

    useEffect(() => {
      // fetch deal info


      axios.get('http://localhost:8080/api/params/recv_number')
        .then((resp1) => {
          if (resp1.data) {
            axios.get(`http://localhost:8080/api/deals/info?token=${sessionStorage.getItem('token')}&deal_id=${dealId}`)
              .then(resp2 => {
                if (resp2.data.status == 'ok') {
                  if(resp2.data.dealStatus=='created'){
                    setPaymentInfo({
                      total: resp2.data.total,
                      phone_number: resp1.data
                    })
                  }else{
                    setActive(3)
                  }
                  
                } else {
                  alert('Deal cannot be found. Please create a new one.')
                  //window.location = '/'
                }
              })
         
          }
        })
    }, [])
    return (
      <div style={{ width: '100%', height: '380px', textAlign: 'center' }}>
        <div style={{ paddingTop: 50 }}>
          <Loader color="dark" style={{ margin: 'auto' }} />
          <Title order={3}>Waiting for you</Title>
          <span>Please send the specified amount to the our middle D17 number.</span><br></br>
          <span>Once you finish sending, please hit next.</span><br></br>

          <br></br>
          <br></br>

          <div style={{
            paddingTop: 10, paddingBottom: 10, paddingLeft: 20, paddingRight: 20,
            border: '2px solid  #333333bf',
            borderStyle: 'dashed',
            borderRadius: 6,
            width: 'max-content',
            margin: 'auto'
          }}>
            Please send <span style={{
              padding: 5,
              borderRadius: 5,
              background: 'linear-gradient(45deg, #4776E6 0%, #8E54E9 100%)',
              color: 'white',
              fontWeight: 'bold'
            }}>{parseFloat(paymentInfo['total']).toFixed(2) || ' Loading.. '} TND</span> to this number <span style={{
              padding: 5,
              borderRadius: 5,
              background: 'linear-gradient(45deg, #4776E6 0%, #8E54E9 100%)',
              color: 'white',
              fontWeight: 'bold'
            }}>{paymentInfo['phone_number'] || ' Loading.. '}</span> via your D17 app.


          </div>

        </div>


      </div>
    )
  }

  function ProcessList({ currentStep }) {

    const list = [
      "Confirming your fiat transaction.",
      "Waiting for Seller to respond.",
      "Seller responded, confirming his transaction.",
      "Forwarding crypto to your wallet."
    ]
    return (
      <div style={{ width: 'max-content', margin: 'auto', textAlign: 'left' }}>
        {list.map((item, index) => <tr style={{ marginBottom: 10 }}>
          <td>{index == currentStep && <Loader color="dark" size={29} style={{ marginBottom: '-8px', boxSizing: 'border-box', padding: '2px' }} />} {index > currentStep && <CircleCheck size={30} style={{ marginBottom: '-8px', opacity: 0 }} />} {index < currentStep && <CircleCheck size={30} style={{ marginBottom: '-8px' }} />}</td>
          <td style={{ paddingLeft: 10, opacity: item.status == 'waiting' ? 0.5 : 1 }}>{item}</td>

        </tr>)}
      </div>
    )
  }

  function Step4() {
    const [currentStep, setCurrentStep] = useState(0)
    const [disliked, setDisliked] = useState(false)

    useEffect(() => {
      setInterval(() => {
        axios.get(`http://localhost:8080/api/deals/status?token=${sessionStorage.getItem('token')}&deal_id=${dealId}`)
          .then((r) => {
            if (r.data.status == 'ok') {
              let scode = 0;
              if (r.data.deal_status == 'buyer-placed') {
                scode = 0; // Confirming your transaction
              }
              if (r.data.deal_status == 'buyer-placed-confirmed') {
                scode = 1; // Waiting for seller to respond
              }
              if (r.data.deal_status == 'seller-placed') {
                scode = 2; // Confirming seller's transaction
              }
              if (r.data.deal_status == 'seller-placed-confirmed') {
                scode = 3; // Forwarding crypto to your wallet
              }
              if (r.data.deal_status == 'deal-closed') {
                scode = 4; // Done : show success screen
              }
              if (r.data.deal_status == 'crypto-forwarded') {
                scode = 4; // Done : show success screen
              }
              if(r.data.deal_status == 'rejected'){
                scode = -1; // Deal is rejected by seller
              }
              setCurrentStep(scode)
            }
          })
      }, 10000)
    }, [])
    return (
      <>
      {currentStep==4&&<>
        <div style={{ height: 350, paddingTop: 50, position: 'relative', textAlign:'center' }}>
          <CircleCheck size={30} />
          <Title order={3}>Success!</Title>
          <Text  mb={10} mt={5} color="dimmed" size="sm">Your deal is successfully closed, your crypto should be in your wallet in any minute.</Text>
          <Text  mb={10} mt={5} color="dimmed" size="sm">Transaction ID: <span style={{
              padding: 5,
              borderRadius: 5,
              background: 'linear-gradient(45deg, #4776E6 0%, #8E54E9 100%)',
              color: 'white',
              fontWeight: 'bold'
            }}>0f23231288df32909932</span></Text>
          
          <Group style={{margin:'auto', width:'fit-content'}} mt={20}>

          <Button onClick={(e)=>{e.preventDefault(); setDisliked(!disliked)}} variant={disliked?"filled":"default"} color={disliked&&"indigo"} leftIcon={<ThumbUp size={16} />}>Like seller</Button>
          <Button variant="default">Browse more offers</Button>

          </Group>
        </div>
      </>}
      {currentStep==-2&&<>
        <div style={{ height: 350, paddingTop: 50, position: 'relative', textAlign:'center' }}>
          <AlertTriangle size={30} />
          <Title order={3}>Deal expired!</Title>
          <Text  mb={10} mt={5} color="dimmed" size="sm">Your deal got expired because the seller did not respond to your deal within 1 Hour. Your money will be transfered to you immediately.</Text>
          <Group style={{margin:'auto', width:'fit-content'}} mt={20}>

          <Button onClick={(e)=>{e.preventDefault(); setDisliked(!disliked)}} variant={disliked?"filled":"default"} color={disliked&&"red"} leftIcon={<ThumbDown size={16} />}>Dislike seller</Button>
          <Button variant="default">Browse more offers</Button>

          </Group>
        </div>
      </>}
      {currentStep==-1&&<>
        <div style={{ height: 350, paddingTop: 50, position: 'relative', textAlign:'center' }}>
          <AlertTriangle size={30} />
          <Title order={3}>Deal got rejected!</Title>
          <Text  mb={10} mt={5} color="dimmed" size="sm">Your deal got rejected by the seller, this might be because the seller does not have the requested amount. Your money will be retransfered to you immediately.</Text>
          <Group style={{margin:'auto', width:'fit-content'}} mt={20}>

          <Button onClick={(e)=>{e.preventDefault(); setDisliked(!disliked)}} variant={disliked?"filled":"default"} color={disliked&&"red"} leftIcon={<ThumbDown size={16} />}>Dislike seller</Button>
          <Button variant="default">Browse more offers</Button>

          </Group>
        </div>
      </>}
      {
        currentStep&&<div style={{ height: 350, paddingTop: 50, position: 'relative' }}>
        <div style={{ textAlign: 'center', width: '100%' }}>
          <Title order={4} mb={5}>Track process</Title>

        </div>

        <ProcessList
          currentStep={currentStep}


        />

        <Alert icon={<InfoCircle size={16} />} title="Tip" color="yellow" style={{ width: 'max-content', margin: 'auto', marginTop: 70, bottom: 0 }}>
          In case the seller did not respond within 1 Hour, you will be automatically refunded without any fees.
        </Alert>

      </div>
      }
      </>
    );
  }


  return (
    <Card >

      <div style={{ width: '100%', display: 'inline-flex', marginBottom: 20 }}>
        <img style={{ height: '30px', marginRight: '10px' }} src={`/crypto/${offerInfo.crypto_id}.svg`} ></img><Title order={3} mb={0}>Buy {offerInfo.crypto_id}</Title>

      </div>

      <Stepper active={active} breakpoint="sm" mt={5} color="dark" completedIcon={<CircleCheck />}>
        <Stepper.Step label="Select Amount">

          <Step1 handleStepSubmit={handleStepSubmit} prevStep={prevStep} form={form['step1']} />

        </Stepper.Step>
        <Stepper.Step label="Enter Address">

          <Step2 handleStepSubmit={handleStepSubmit} prevStep={prevStep} form={form['step2']} />

        </Stepper.Step>
        <Stepper.Step label="Make transaction">


          <Step3 />


          <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} onClick={(e) => { e.preventDefault(); handleStepSubmit() }} disabled={(active == 3)} size="md" style={{ margin: 'auto', marginRight: '0px', float: 'right' }}>Next</Button>



        </Stepper.Step>
        <Stepper.Step label="Recieve USDT" >


          <Step4 />



        </Stepper.Step>

        <Stepper.Completed>
          Completed, click back button to get to previous step
        </Stepper.Completed>
      </Stepper>




    </Card>
  )
}


export default function BuyCrypto() {
  // check if user is logged in
  if (!sessionStorage.getItem('token')) {
    window.location = `/login?session_expired&redirect_to=${btoa(window.location)}`;

  } else {
    axios.post('http://localhost:8080/api/verif_token', { token: sessionStorage.getItem('token') })
      .then((resp) => {
        if (resp.data.status == 'auth-error') {
          // session token expired
          sessionStorage.clear();
          window.location = `/login?session_expired&redirect_to=${btoa(window.location)}`;
        }
      })
  }
  return (
    <Container style={{ paddingTop: 20, paddingBottom: 20 }}>
      <BuyPanel />

    </Container>
  );
}



