import React from 'react';
import { MultiSelect, Avatar, Group, Text, Title } from '@mantine/core';
import { Select } from '@mantine/core';
import { CurrencyBitcoin } from 'tabler-icons-react';


const data = [{

    label:'Bitcoin',
    value:'BTC',
},
{
    label:'Tether',
    value:'USDT',
},
{
    label:'Ethereum',
    value:'ETH',
},
{
    label:'Binance',
    value:'BNB',
},
]



const SelectItem = React.forwardRef(
    ({ label, value, reservoir, bestQ, ...others }, ref) => (
        <div ref={ref} key={value} {...others}>
            <Group noWrap>
                <Avatar src={`/crypto/${value.toUpperCase()}.svg`} />

                <div>
                    <Text>{label}</Text>
                </div>
            </Group>
        </div>
    )
);

export default function CryptoSelect(props) {
    return (
        <Select
            style={{
                minWidth:'300px',
            }}
            value={props.value}
            leftSection={<CurrencyBitcoin size={14} />}
            itemComponent={SelectItem}
            data={data}
            onChange={(value)=>{props.handleChange('crypto_id', value)}}
            defaultValue={props.defaultValue}
            searchable
            maxDropdownHeight={200}
           
        />
    );
}