function OfferSearch(props) {
    const data = [{

        label: 'Bitcoin',
        value: 'BTC',
    },
    {
        label: 'Tether',
        value: 'USDT',
    },
    {
        label: 'Ethereum',
        value: 'ETH',
    },
    {
        label: 'Binance',
        value: 'BNB',
    },
    ]
    const SelectItem = React.forwardRef(
        ({ label, value, reservoir, bestQ, ...others }, ref) => (
            <div ref={ref} key={value} {...others}>
                <Group noWrap>
                    <Avatar src={`/crypto/${value.toUpperCase()}.svg`} />

                    <div>
                        <Text>{label}</Text>
                    </div>
                </Group>
            </div>
        )
    );
    return (
        <Group style={props.style}>
            <Select

                value='All'
                leftSection={<CurrencyEthereum size={14} />}
                itemComponent={SelectItem}
                data={data}
                defaultValue="Bitcoin"
                searchable
                maxDropdownHeight={200}
                size="lg"
                styles={{
                    input: {
                        border: 'none',
                        backgroundColor: '#ffffff24',
                        fontSize: '15px'
                    }
                }}
            />
            <TextInput placeholder="Amount in USD.." size="lg" styles={{
                input: {
                    border: 'none',
                    backgroundColor: '#ffffff24',
                    fontSize: '15px',
                    color: 'white'
                }
            }} />
            <Button color="dark" size="lg" variant="gradient" leftIcon={<Search />}
                gradient={{ from: '#4776E6', to: '#8E54E9' }}>Explore Offers</Button>
        </Group>
    )
}

export default function Reservoir() {
    const { classes } = useStyles()
    const [form, setForm] = useState({ crypto_id: undefined })
    function handleChange(name, value) {
        const auxForm = { ...form };
        auxForm[name] = value;
        setForm(auxForm);
    }
    function handleSubmit() {
        let url = `/reservoir?crypto_id=${form['crypto_id']}`

        if (form['amount']) {
            url += `&amount=${form['amount']}`
        }
        if (form['max_quote']) {
            url += `&max_quote=${form['max_quote']}`
        }

        window.location = url;
    }
    useEffect(() => {
        const params_list = window.location.search.substring(1).split('&').map(item => { return { name: item.split('=')[0], value: item.split('=')[1] || true } });

        const q = {}
        for (var i = 0; i < params_list.length; i++) {
            if (params_list[i].name && (params_list[i].name == 'crypto_id')) {
                q[params_list[i].name] = params_list[i].value
            }
            if (params_list[i].name && (params_list[i].name == 'amount')) {
                q[params_list[i].name] = params_list[i].value

            }
            if (params_list[i].name && (params_list[i].name == 'max_quote')) {
                q[params_list[i].name] = params_list[i].value


            }
        }

        setForm(q)
        //console.log(params_list)
        console.log(form)



    }, [])

    return (
        <div className={classes.container}>
            <div className={classes.panel}>
                <MantineProvider theme={{ colorScheme: 'dark' }} withNormalizeCSS>

                    <div style={{ margin: 'auto', padding: '20px', paddingTop: '30px', paddingBottom: '30px' }}>
                        <Title mb={5}>Search for offer</Title>
                        
                    </div>
                    <OfferSearch style={{ marginTop: 50 }} />

                </MantineProvider>

            </div>

            <Box mt={20} >
                <Box mb={10}>
                <Button size="sm" variant="filled" color="dark" style={{ float: 'right' }} onClick={(e) => { e.preventDefault(); window.location = '/dashboard#offers-create' }}>Post Offer</Button>
                <Title order={2}>
                    Browse our offer listings
                </Title>
                <Text size="sm" className={classes.description}>
                    All offers listed within our resevoir are 100% legit and verified by our Blockchain Exploring algorithms.Once the wallet's owner no longer<br></br>  have sufficient funds as listed the offer will be automatically droped.
                </Text>
                </Box>
                

                <DataTable />
            </Box>




        </div>
    );
}