import { Input } from '@mantine/core';
import { Title } from '@mantine/core';
import { Group } from '@mantine/core';
import { Select } from '@mantine/core';
import { Alert } from '@mantine/core';
import { Button } from '@mantine/core';
import { Container } from '@mantine/core';
import { InputWrapper, Card } from '@mantine/core';
import axios from 'axios';
import React, { useState } from 'react';
import { AlertCircle } from 'tabler-icons-react';
import CryptoSelect from './../Components/CryptoSelect';



export default function CreateOffer(props) {
    const [isLoading, setLoading] = useState(false);
    const [respmsg, setRespmsg] = useState();

    const [form, setForm] = useState({
        crypto_id: {
            value: '',
            error: undefined
        },
        min_amount: {
            value: '',
            error: undefined
        },
        max_amount: {
            value: '',
            error: undefined
        },
        sell_quote: {
            value: '',
            error: undefined
        },
        payment_method: {
            value: '',
            error: undefined
        },
        accept_number: {
            value: '',
            error: undefined
        }
    });

    function setError(name, error) {
        const auxForm = { ...form };
        auxForm[name]['error'] = error;
        setForm(auxForm);
    }

    function handleChange(name, value) {
        const auxForm = { ...form };
        auxForm[name].value = value;
        auxForm[name].error = undefined;
        setForm(auxForm);
        console.log(form);
    }

    function handleSubmit(e) {
        e.preventDefault();
        console.log(form)
        setLoading(true);

        let error = false;
        if (!form['crypto_id']['value'] || (form['crypto_id']['value'].length == 0)) {
            // Set error
            setError('crypto_id', 'You must select a cryptocurrency!')
            error = true;
        }
        if (!form['min_amount']['value'] || (form['min_amount']['value'].length == 0)) {
            // Set error
            setError('min_amount', 'Minimum amount cannot be empty!')
            error = true;
        }
        if (!form['max_amount']['value'] || (form['max_amount']['value'].length == 0)) {
            // Set error
            setError('max_amount', 'Maximum amount cannot be empty!')
            error = true;
        }
        if (!form['sell_quote']['value'] || (form['sell_quote']['value'].length == 0)) {
            // Set error
            setError('sell_quote', 'Quote cannot be empty!')
            error = true;
        }

        if (!form['payment_method'].value || (form['payment_method'].value == 0)) {
            setError('payment_method', 'You must select a payment method.')
            error = true;

        }
        if (!form['accept_number'].value || (form['accept_number'].value == 0)) {
            setError('accept_number', 'Phone number cannot be empty.')
            error = true;

        }


        if (error) {
            setLoading(false)

            return;
        }else{
           
            axios.post('http://localhost:8080/api/offers/create', {
                token:sessionStorage.getItem('token'),
                crypto_id:form['crypto_id'].value,
                min_amount:form['min_amount'].value,
                max_amount:form['max_amount'].value,
                sell_quote:form['sell_quote'].value,
                accept_number:form['accept_number'].value
            })
                .then((resp)=>{
                    console.log(resp)
                    if(resp.data.status=='ok'){
                        // success
                        props.navigateTab('#offers');
                    }
                    else if(resp.data.status=='error'){
                        // data entry error
                        if(resp.data.fields&&(resp.data.fields.length > 0)){
                            const errors = resp.data.fields;
                            for (let i=0; i<errors.length; i++){
                              setError(errors[i].name, errors[i].label);
                            }
                        }
                    }
                    else if(resp.data.status == 'auth-error'){
                        sessionStorage.clear();
                        window.location = '/login?session_expired'
                    }else if(resp.data.status == 'fatal-error'){
                        setRespmsg({ status: 'fatal_error', label: 'Internal error, please try again later.' })
                        
                    }
                 setLoading(false)

                })
                .catch(()=>{
                    setRespmsg({ status: 'fatal_error', label: 'Internal error, please try again later.' })
                    setLoading(false)
               
                })

        }
    }

    return (
        <>
            <InputWrapper
                    id="crypto_id"
                    required
                    label="Cryptocurrency"
                    style={{ marginBottom: 5, marginTop:5 }}
                >
                <CryptoSelect handleChange={handleChange} />
                </InputWrapper>
            <Group grow>
                <InputWrapper
                    id="min_amount"
                    required
                    label="Minimum Amount"
                    style={{ marginBottom: 5, marginTop:5 }}
                >
                    <Input id="min_amount" name="min_amount" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} error={form['min_amount'].error || undefined} ></Input>
                </InputWrapper>
                <InputWrapper
                    id="max_amount"
                    required
                    label="Maximum Amount"
                    style={{ marginBottom: 5, marginTop:5 }}

                >
                    <Input id="max_amount" name="max_amount" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} error={form['max_amount'].error || undefined} ></Input>
                </InputWrapper>
            </Group>
            <InputWrapper
                id="quote"
                required
                label="Quote"
                style={{ marginBottom: 5 }}
            >
                <Input id="quote" name="sell_quote" onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} error={form['sell_quote'].error || undefined} ></Input>
            </InputWrapper>
            <InputWrapper
                id="payment_method"
                required
                label="Payment Method "
                style={{ marginBottom: 5 }}
            >
                <Select
                    data={[
                        { value: 'D17', label: 'D17 Safe Exchange' },
                    ]}
                    name="payment_method"
                    onChange={(value) => { handleChange('payment_method', value) }}
                    error={form['payment_method'].error || undefined}
                />

            </InputWrapper>
            {form['payment_method'].value == 'D17' && <InputWrapper
                id="recv_number"
                required
                label="D17 Phone number :"
                description="This is the phone number associated to the account that you want to recieve your funds in, please type it correctly."
                style={{ marginBottom: 5 }}

            >
                <Input id="recv_number" name="accept_number" placeholder='+216 00 000 000' onChange={(e) => { handleChange(e.currentTarget.name, e.currentTarget.value) }} error={form['accept_number'].error || undefined}  ></Input>
            </InputWrapper>}

            {respmsg&&<Alert icon={<AlertCircle size={16} />} title="Ops" color="red">
                    {respmsg.label}
            </Alert>}
            <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} style={{ float: 'right', marginTop: '20px' }} loading={isLoading} onClick={(e)=>handleSubmit(e)}>Publish</Button>
        </>

    );
}