import { createStyles } from '@mantine/core';
import axios from 'axios';
import React, { useEffect } from 'react';
import { Checklist, Exchange } from 'tabler-icons-react';
import RelativeTime from '@yaireo/relative-time'


const useStyles = createStyles((theme)=>({
    row: {
        width: '100%',
        borderBottom: '1px solid whitesmoke',
        display:'inline-flex',
        borderLeft:'3px solid black',
        cursor:'pointer',
        backgroundColor:'white',
        '&:hover': {
            backgroundColor: 'whitesmoke !important',
        },
    },
    bodyCase: {
        paddingTop: '10px',
        paddingBottom: '10px',
        paddingLeft: '20px',
        paddingRight: '20px',
        fontSize: '14px',
    },
}))

const icons = {
    'exchange':Exchange,
    'success': Checklist,

}

function Notification(props){
    const {classes, cx} = useStyles();
    const Icon = icons[props.icon]
    return (
        <div className={classes.row} style={{ borderLeft: props.seen==0?'3px solid #087f5b':'3px solid black'}}>
            <div className={classes.bodyCase}>
                <Icon size={20} />
            </div>
            <div className={classes.bodyCase} style={{width:'inherit'}}>
                {props.text}
            </div>
            <div className={classes.bodyCase} style={{textAlign:'right', width:'-webkit-fill-available'}}>
                <span style={{opacity:0.5}}>{new RelativeTime().from(props.time)}</span>
            </div>
        </div>
    )
}
export default function Notifications({data}){
   
    useEffect(()=>{
       if(data.length > 0){
        axios.post(`http://localhost:8080/api/notifications/mark_seen`, {
            token:sessionStorage.getItem('token'),
            lastSeenId:data[0].id
        })
       }
    },[data])
    
    return (
       <>
            {data.map(item => <Notification text={item.body} time={item.creation_time} seen={item.seen} icon={'exchange'} />)}
       </>
        
    )
}