import React, { useEffect, useState } from 'react';
import { createStyles, Group, Paper, SimpleGrid, Text } from '@mantine/core';
import {
  UserPlus,
  Discount2,
  Receipt2,
  Coin,
  ArrowUpRight,
  ArrowDownRight,
  List,
  Eye,
  CurrencyDollar,
  Clock,
} from 'tabler-icons-react';
import axios from 'axios';

const useStyles = createStyles((theme) => ({
  root: {
    marginBottom: 20,
  },

  value: {
    fontSize: 24,
    fontWeight: 700,
    lineHeight: 1,
  },

  diff: {
    lineHeight: 1,
    display: 'flex',
    alignItems: 'center',
  },

  icon: {
    color: theme.colorScheme === 'dark' ? theme.colors.dark[3] : theme.colors.gray[4],
  },

  title: {
    fontWeight: 700,
    textTransform: 'uppercase',
  },
}));




export default function Stats() {
  const { classes } = useStyles();
  const [data, setData] = useState({n_listed_offers:'N/C', total_views:'N/C', avg_resp_time:'N/C'})
  useEffect(()=>{
    axios.get(`http://localhost:8080/api/widgets?token=${sessionStorage.getItem('token')}`)
      .then((resp)=>{
        if(resp.data.status=='ok'){
          setData({
            n_listed_offers:resp.data['n_listed_offers'],
            total_views:resp.data['total_views'],
            avg_resp_time:(resp.data['avg_resp_time']==-1)?'N/C':resp.data['avg_resp_time'],
            total_sold:resp.data['total_sold']
          })
        }
        else if(resp.status == 'auth-error'){
          sessionStorage.clear()
          window.location = '/login?session_expired'
        }
      })
  }, [])
  return (
    <div className={classes.root}>
      <SimpleGrid
        cols={4}
        breakpoints={[
          { maxWidth: 'md', cols: 2 },
          { maxWidth: 'xs', cols: 1 },
        ]}
      >
        <Paper withBorder p="md" radius="md" key="total_listed_offers">
          <Group position="apart">
            <Text size="xs" color="dimmed" className={classes.title}>
              Total Listed Offers
            </Text>
            <List className={classes.icon} size={22} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={25}>
            <Text className={classes.value}>{data['n_listed_offers']}</Text>

          </Group>

        </Paper>
        <Paper withBorder p="md" radius="md" key="total_number_views">
          <Group position="apart">
            <Text size="xs" color="dimmed" className={classes.title}>
              Total Number Of Views
            </Text>
            <Eye className={classes.icon} size={22} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={25}>
            <Text className={classes.value}>{data['total_views']}</Text>

          </Group>

        </Paper>
        <Paper withBorder p="md" radius="md" key="total_sold">
          <Group position="apart">
            <Text size="xs" color="dimmed" className={classes.title}>
              Total Sold
            </Text>
            <Coin className={classes.icon} size={22} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={25}>
            <Text className={classes.value}>{data.total_sold?data.total_sold:'0'} USD</Text>

          </Group>

        </Paper>
        <Paper withBorder p="md" radius="md" key="avg_rep_time">
          <Group position="apart">
            <Text size="xs" color="dimmed" className={classes.title}>
              Average Response Time
            </Text>
            <Clock className={classes.icon} size={22} />
          </Group>

          <Group align="flex-end" spacing="xs" mt={25}>
            <Text className={classes.value}>
              {data['avg_resp_time']}
            </Text>

          </Group>

        </Paper>
      </SimpleGrid>
    </div>
  );
}