import { createStyles } from '@mantine/core';
import { Group } from '@mantine/core';
import { Grid } from '@mantine/core';
import { Button } from '@mantine/core';
import { Title, Alert, TextInput } from '@mantine/core';
import React, {useState} from 'react';
import axios, { AxiosError } from 'axios';



const useStyles = createStyles((theme)=>({
    name:{
        fontSize:15,
        fontWeight:'bold',
        paddingRight:'10px'
    },
    value:{
        fontSize:15

    }
}))

export default function DealDetails({data}){
    const {classes, cs} = useStyles();
    const [transaction_id, setTransactionId] = useState('')
    const [feedback, setFeedback] = useState({})
    function handleTransactionSubmit(){
        if(!transaction_id){
            setFeedback({status:'error', label:'Transaction ID cannot be empty.'})
            return;
        }
        axios.post('http://localhost:8080/api/deals/accept', {
            token:sessionStorage.getItem('token'),
            deal_id:data.id,
            transaction_id:transaction_id
        })
            .then((r)=>{
                if(r.data.status=='ok'){
                    // Done
                    setFeedback({status:'ok', label:'Your transaction is now being confirmed, you will be notified.'})
                }else{
                    setFeedback({status:'error', label:'Error occured, please re-login and try again.'})
                }
            })
            .catch(error=>{
                console.log(error)
                setFeedback({status:'error', label:'Network error, please check your connection.'})
            })
    }
    return (
        <Grid >
            <Grid.Col span={12}>
            <tr >
                <td className={classes.name}>Deal Number </td>
                <td className={classes.value}>{data.id}</td>
            </tr>
            <tr>
                <td className={classes.name}>Cryptocurrency </td>
                <td className={classes.value}>{data.crypto_id}</td>
            </tr>
            <tr>
                <td className={classes.name}>Amount </td>
                <td className={classes.value}>{data.amount} USD</td>
            </tr>
            <tr>
                <td className={classes.name}>Quote </td>
                <td className={classes.value}>{data.sell_quote}</td>
            </tr>
            <tr>
                <td className={classes.name}>Opening time </td>
                <td className={classes.value}>{data.creation_time}</td>
            </tr>
            <tr>
                <td className={classes.name}>Expiring time </td>
                <td className={classes.value}>{data.creation_time + 3600000}</td>
            </tr>
            <tr>
                <td className={classes.name}>Status </td>
                <td className={classes.value}>{data.status}</td>
            </tr>
            <tr>
                <td className={classes.name}>Buyer receiving address </td>
                <td className={classes.value}>
                    <div style={{display:'inline-flex', width:'100%'}}>
                        <input value={'0xKRYPT0N37777'} style={{width:'100%'}} />
                        <Button size="xs" ml={2}>Copy</Button>
                    </div>
                
                </td>
            </tr>
            <tr>
                <td className={classes.name}>Amount to send </td>
                <td className={classes.value}>{data.amount} USD</td>
            </tr>
            <tr>
                <td className={classes.name}>In exchange with </td>
                <td className={classes.value}>{parseFloat(data.amount)*parseFloat(data.sell_quote) - parseFloat(data.amount)*parseFloat(data.sell_quote) * 0.025} TND</td>
            </tr>
            </Grid.Col>
           
    
            <Grid.Col span={12} style={{textAlign:'center'}} mt={10}>
                <img src="/qr.png" style={{width:'100%'}} />
            </Grid.Col>

            <Grid.Col span={12} mt={10}>
                <Title order={3}>Once you made the transaction</Title>
                <TextInput name="transaction_id" label="Transaction ID" onChange={(e)=>{setTransactionId(e.currentTarget.value)}} />
                <Button variant="default" fullWidth mt={3} onClick={(e)=>{e.preventDefault(); handleTransactionSubmit()}}>Submit</Button>
                {feedback.label&&<Alert color={feedback.status=='ok'?'cyan':'red'}>{feedback.label}</Alert>}
            </Grid.Col> 
            
            

        </Grid>
    )
}