import { Alert } from '@mantine/core';
import { Button } from '@mantine/core';
import { ActionIcon } from '@mantine/core';
import { Modal } from '@mantine/core';
import { Group } from '@mantine/core';
import { Title } from '@mantine/core';
import { AppShell } from '@mantine/core';
import { useViewportSize } from '@mantine/hooks';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { AlertCircle, ArrowAutofitContent, ArrowBack, ArrowBigRight, ArrowLeft, Trash } from 'tabler-icons-react';
import { DataTable, DataTableDashboard, DealsTable } from '../Components/Listing';
import Notifications from '../Components/Notifications';
import Sidebar from '../Components/Sidebar';
import Stats from '../Components/Stats';
import CreateOffer from './CreateOffer';
import DealDetails from './DealDetails';
import Settings from './Settings';



export default function Dashboard(props) {

    const [currentTab, setCurrentTab] = useState(window.location.hash || '#dashboard');
    

  
    var { height, width } = useViewportSize();
    height = height - 60;
    return (

        <AppShell
            padding="md"
            navbar={<Sidebar navigate={setCurrentTab} />}
        >


            {currentTab == '#dashboard' && <div >
                <Stats />
                <Alert mb={10} icon={<AlertCircle size={16} />} title="Tip" color="yellow" withCloseButton>
                    Faster response time means better offer ranking, respond faster to your custumer to recieve more!
                </Alert>

                <Title order={4} mb={10} mt={20}>Pending Deals :</Title>
                <DealsTable />

                <Title order={4} mb={10} mt={20}>My offers :</Title>
                <DataTableDashboard navigate={setCurrentTab} />
            </div>}
            {currentTab == '#deals' && <div>

                <Title order={3} mb={10} mt={20}>My Deals : </Title>
                <Alert mb={10} icon={<AlertCircle size={16} />} title="Tip" color="yellow" withCloseButton>
                    Faster response time means better offer ranking, respond faster to your custumer to recieve more!
                </Alert>
                <DealsTable />
            </div>}
            {currentTab == '#offers' && <div >
                <Group grow>
                    <Title order={3} mb={10} mt={20}>My Offers : </Title>
                    <div>
                        <Button color="dark" variant="gradient" gradient={{ from: '#4776E6', to: '#8E54E9' }} style={{ float: 'right' }} onClick={(e) => { e.preventDefault(); setCurrentTab('#offers-create') }}>Create new offer</Button>
                    </div>

                </Group>
                <Alert mb={10} icon={<AlertCircle size={16} />} title="Tip" color="yellow" withCloseButton>
                    Faster response time means better offer ranking, respond faster to your custumer to recieve more!
                </Alert>
                <DataTableDashboard  navigate={setCurrentTab}  />
            </div>}
            {currentTab == '#offers-create' && <div>

                <Group mb={10} mt={20}>
                    <ActionIcon size="md" onClick={(e) => { e.preventDefault(); setCurrentTab('#offers') }}>
                        <ArrowLeft size={20}></ArrowLeft>
                    </ActionIcon>
                    <Title order={3}>Create offer:</Title>

                </Group>
                <Alert mb={10} icon={<AlertCircle size={16} />} title="Tip" color="yellow" withCloseButton>
                    Faster response time means better offer ranking, respond faster to your custumer to recieve more!
                </Alert>
                <CreateOffer navigateTab={setCurrentTab} />
            </div>}
            {currentTab == '#notifications' && <div>
                <Title order={3} mb={10} mt={20}>Notifications</Title>
                <Notifications data={props.notifs} />
            </div>}
            {currentTab == '#settings' && <div>
                <Title order={3} mb={10} mt={20}>Account Settings</Title>
                <Settings />
            </div>}

        </AppShell>

    );
}